import React, { Component } from 'react';
import './App.css';
import img_elImage from './images/Roll2Screen_elImage_62945.png';
import Component1 from './Component1';
import Rolluser from './Rolluser';

// UI framework component imports
import Button from 'muicss/lib/react/button';

export default class RollScreen extends Component {

  // Properties used by this component:
  // appActions, deviceInfo

  constructor(props) {
    super(props);
    
    this.state = {
    };
  }

  componentDidMount() {
    {
      let dataSheet = this.props.appActions.dataSheets['aquaroll'];
      let serviceOptions = this.props.appActions.serviceOptions_aquaroll;
      if ( !this.props.appActions.dataSheetLoaded['aquaroll']) {
        serviceOptions.servicePath = dataSheet.expandSlotTemplateString("5mgja99r-roll", this.props.appActions.dataSlots);
        this.props.appActions.loadData_gsheet3(dataSheet, serviceOptions, true);
        this.props.appActions.dataSheetLoaded['aquaroll'] = true;
      }
      this._dataSheetLoadTimer_aquaroll = setInterval(() => {  // Reload data regularly
        serviceOptions.servicePath = dataSheet.expandSlotTemplateString("5mgja99r-roll", this.props.appActions.dataSlots);
        this.props.appActions.loadData_gsheet3(dataSheet, serviceOptions, false);
      }, 10000);
    }
    {
      let dataSheet = this.props.appActions.dataSheets['checkin'];
      let serviceOptions = this.props.appActions.serviceOptions_checkin;
      if ( !this.props.appActions.dataSheetLoaded['checkin']) {
        serviceOptions.servicePath = dataSheet.expandSlotTemplateString("5mgja99r-empcheckin", this.props.appActions.dataSlots);
        this.props.appActions.loadData_gsheet2(dataSheet, serviceOptions, true);
        this.props.appActions.dataSheetLoaded['checkin'] = true;
      }
      this._dataSheetLoadTimer_checkin = setInterval(() => {  // Reload data regularly
        serviceOptions.servicePath = dataSheet.expandSlotTemplateString("5mgja99r-empcheckin", this.props.appActions.dataSlots);
        this.props.appActions.loadData_gsheet2(dataSheet, serviceOptions, false);
      }, 10000);
    }
  }

  componentWillUnmount() {
    clearInterval(this._dataSheetLoadTimer_aquaroll);
    clearInterval(this._dataSheetLoadTimer_checkin);
  }

  componentDidUpdate() {
  }

  componentWillReceiveProps(nextProps) {
  }

  onClick_elButton = (ev) => {
    // Go to screen 'Start'
    this.props.appActions.goToScreen('start', { transitionId: 'fadeIn' });
  
  }
  
  
  onClick_elButton2 = (ev) => {
    // Go to screen 'Roll 2'
    this.props.appActions.goToScreen('roll2', { transitionId: 'fadeIn' });
  
  }
  
  
  render() {
    let layoutFlowStyle = {};
    let baseStyle = {};
    if (this.props.transitionId && this.props.transitionId.length > 0 && this.props.atTopOfScreenStack && this.props.transitionForward) {
      baseStyle.animation = '0.25s ease-in-out '+this.props.transitionId;
    }
    if ( !this.props.atTopOfScreenStack) {
      layoutFlowStyle.height = '100vh';
      layoutFlowStyle.overflow = 'hidden';
    }
    
    const dataSheet_checkin = this.props.dataSheets['checkin'];
    const style_elBackground = {
      width: '100%',
      height: '100%',
     };
    const style_elBackground_outer = {
      backgroundColor: '#f6f6f6',
     };
    const style_elImage = {
      backgroundImage: 'url('+img_elImage+')',
      backgroundRepeat: 'no-repeat',
      backgroundPosition: '50% 50%',
      backgroundSize: 'cover',
     };
    const style_elButton = {
      display: 'block',
      color: 'white',
      textAlign: 'center',
      cursor: 'pointer',
      pointerEvents: 'auto',
     };
    const style_elButton2 = {
      display: 'block',
      color: 'white',
      textAlign: 'center',
      cursor: 'pointer',
      pointerEvents: 'auto',
     };
    const style_elText = {
      fontSize: 16.1,
      color: 'rgba(0, 0, 0, 0.8500)',
      textAlign: 'center',
     };
    let transformPropValue_text2 = (input) => {
      // This function modifies the value for property 'text'.
      // There is a variable named 'input' that provides the property value.
      //
      // Handy example code snippets
      //
      // Update current component’s State from 0 to n
      //
      // this.setState({visualStateIndexOverride: 1})
      //
      // Playing with Data slot values
      //
      // Get data slot value
      //
      // var exampleValue="";
      // exampleValue=this.props.appActions.dataSlots['ds_Example'];
      //
      // Update data slot value
      //
      // var exampleValue="foo";
      // this.props.appActions.updateDataSlot('ds_Example', exampleValue);
      var d = new Date();
      var n = d.toLocaleDateString();
      input = n;
      return input;
    }
    
    const value_text2 = transformPropValue_text2((this.props.appActions.dataSlots ? this.props.appActions.dataSlots['ds_date'] : ''));
    
    const style_elText2 = {
      color: 'rgba(0, 0, 0, 0.8500)',
      textAlign: 'center',
     };
    const style_elText3 = {
      color: 'rgba(0, 0, 0, 0.8500)',
      textAlign: 'left',
     };
    
    // Source items and any special components used for list/grid element 'list'.
    let items_list = [];
    let listComps_list = {};
    let filterItems_list = items => {
      var d = new Date();
      var n = d.toLocaleDateString();
      let filteredItems = [];
      var filterStrd = n; 
      let filterStr=this.props.appActions.dataSlots['ds_filter']; // Data slot value
      filterStr=filterStr.toLowerCase(); // Lowercase the filter string
      if (filterStr == ""){
      //filterStr = "xxx";
      }
      // Loop through all items and add item to new array if item includes filter string
      items.forEach(item => {;
      if (item['datetime'].toLowerCase().includes(filterStrd)) {
        if (item['checkedin'].toLowerCase().includes('yes')){
          if (item['first'].toLowerCase().includes(filterStr)) {
           filteredItems.push(item);
          }else if (item['last'].toLowerCase().includes(filterStr)){
           filteredItems.push(item);
          }else if (item['dept'].toLowerCase().includes(filterStr)){
          filteredItems.push(item);
         }  
        }
      }
      });
      items=filteredItems; 
      
      return items;
    }
    items_list = items_list.concat(filterItems_list(this.props.appActions.getDataSheet('checkin').items));
    
    const style_elList = {
      height: 'auto',  // This element is in scroll flow
     };
    
    return (
      <div className="AppScreen RollScreen" style={baseStyle}>
        <div className="background">
          <div className="containerMinHeight elBackground" style={style_elBackground_outer}>
            <div className="appBg" style={style_elBackground} />
          </div>
        </div>
        
        <div className="layoutFlow" style={layoutFlowStyle}>
          <div className="elImage">
            <div style={style_elImage} />
          </div>
          
          <div className="elButton">
            <Button className="actionFont" style={style_elButton}  color="accent" onClick={this.onClick_elButton} >
              {this.props.locStrings.screen5_button_708422}
            </Button>
          </div>
          
          <div className="elButton2">
            <Button className="actionFont" style={style_elButton2}  color="accent" onClick={this.onClick_elButton2} >
              {this.props.locStrings.roll_button2_945881}
            </Button>
          </div>
          
          <div className="elText">
            <div className="systemFontBold" style={style_elText}>
              <div>{this.props.locStrings.screen5_text_879433}</div>
            </div>
          </div>
          
          <div className="elText2">
            <div className="baseFont" style={style_elText2}>
              <div>{value_text2 !== undefined ? value_text2 : (<span className="propValueMissing">{this.props.locStrings.screen5_text2_117405}</span>)}</div>
            </div>
          </div>
          
          <div className="hasNestedComps elComponent">
            <div>
              <Component1 ref={(el)=> this._elComponent = el} appActions={this.props.appActions} deviceInfo={this.props.deviceInfo} locStrings={this.props.locStrings} />
            </div>
          </div>
          
          <div className="elText3">
            <div className="baseFont" style={style_elText3}>
              <div>{this.props.locStrings.roll_text3_338722}</div>
            </div>
          </div>
          
          <div className="hasNestedComps elList">
            <ul style={style_elList}>
              {items_list.map((row, index) => {
                let itemComp = (row._componentId)
                    ? listComps_list[row._componentId]
                    : <Rolluser dataSheetId={'checkin'} dataSheetRow={row} {...{ 'first': row['first'], 'last': row['last'], 'dept': row['dept'], }} appActions={this.props.appActions} deviceInfo={this.props.deviceInfo} locStrings={this.props.locStrings} />;
                return (<li key={row.key}>
                    {itemComp}
                  </li>);
              })}
              <div className="marker" ref={(el)=> this._elList_endMarker = el} />
            </ul>
          </div>
        </div>
        
      </div>
    )
  }
  
}
