import React, { Component } from 'react';
import './App.css';

// UI framework component imports
import Button from 'muicss/lib/react/button';

export default class Guest extends Component {

  // This component doesn't use any properties

  constructor(props) {
    super(props);
    
    this.state = {
      txtFirstName: '',
      txtLastName: '',
    };
  }

  componentDidMount() {
  }

  componentWillUnmount() {
  }

  componentDidUpdate() {
  }

  componentWillReceiveProps(nextProps) {
  }

  textInputChanged_txtFirstName = (event) => {
    this.setState({txtFirstName: event.target.value});
  }
  
  textInputChanged_txtLastName = (event) => {
    this.setState({txtLastName: event.target.value});
  }
  
  onClick_elButton = (ev) => {
    let newVal = "button";
    
    let transformValue = (input) => {
      // This function modifies the value written to the target data slot.
      // There is a variable named 'input' that provides the input value.
      var d = new Date();
      var n = d.toLocaleDateString();
      //this.props.appActions.updateDataSlot('ds_date', n);
      n = n.replace( /,/g, "" );
      input = n;
      return input;}
    newVal = transformValue(newVal);
    
    this.props.appActions.updateDataSlot('ds_date', newVal);
  
    this.sendData_button_to_checkin();
  
    this.sendData_button_to_aquaeemps();
  
    newVal = this.state.txtFirstName;
    this.props.appActions.updateDataSlot('ds_first', newVal);
  
    newVal = this.state.txtLastName;
    this.props.appActions.updateDataSlot('ds_last', newVal);
  
    //let first=this.props.appActions.dataSlots['ds_first']; 
    //let last=this.props.appActions.dataSlots['ds_last']; 
    //var d = new Date();
    //var n = d.toLocaleString();
    //n = n.replace( /,/g, "" );
    //alert(n + "\n" + first + " " + last + "\nYou have successfully checked in! \nHave a great day!\n\n\n\n\n\n\n\n");
    var url = "https://aquacheckin.github.io/popup.html";
    var title = "Please Wait";
    var w = 300;
    var h = 300;
    var top = 200; //window.outerHeight / 2 + window.screenY - ( h / 2)
    var left = window.outerWidth / 2 + window.screenX - ( w / 2)
    window.setTimeout(function(){window.location.href=window.location.href},3000);
    var win = window.open(url, 'window', 'toolbar=no, location=no, directories=no, status=no, menubar=no, scrollbars=no, resizable=yes, copyhistory=no, width='+w+', height='+h+', top='+top+', left='+left);
    setTimeout(function () { win.close();}, 3000);
  
  }
  
  
  sendData_button_to_checkin = () => {
    const dataSheet = this.props.appActions.getDataSheet('checkin');
  
    let row = this.props.dataSheetRow || {
    };
    row = { ...row, 
      last: this.state.txtLastName,
      first: this.state.txtFirstName,
      datetime: (this.props.appActions.dataSlots ? this.props.appActions.dataSlots['ds_date'] : ''),
      checkedin: (this.props.appActions.dataSlots ? this.props.appActions.dataSlots['ds_here'] : ''),
      dept: (this.props.appActions.dataSlots ? this.props.appActions.dataSlots['ds_guest'] : ''),
    };
    if (this.props.dataSheetId === dataSheet.id) {
      this.props.appActions.updateInDataSheet('checkin', row);
    } else {
      this.props.appActions.addToDataSheet('checkin', row);
    }
  }
  
  
  sendData_button_to_aquaeemps = () => {
    const dataSheet = this.props.appActions.getDataSheet('aquaeemps');
  
    let row = this.props.dataSheetRow || {
    };
    row = { ...row, 
      first: this.state.txtFirstName,
      last: this.state.txtLastName,
    };
    if (this.props.dataSheetId === dataSheet.id) {
      this.props.appActions.updateInDataSheet('aquaeemps', row);
    } else {
      this.props.appActions.addToDataSheet('aquaeemps', row);
    }
  }
  
  
  render() {
    
    const style_elTxtFirstName = {
      display: 'block',
      backgroundColor: 'white',
      paddingLeft: '1rem',
      boxSizing: 'border-box', // ensures padding won't expand element's outer size
      pointerEvents: 'auto',
     };
    
    const style_elTxtLastName = {
      display: 'block',
      backgroundColor: 'white',
      paddingLeft: '1rem',
      boxSizing: 'border-box', // ensures padding won't expand element's outer size
      pointerEvents: 'auto',
     };
    const style_elButton = {
      display: 'block',
      fontSize: 11.4,
      color: 'white',
      textAlign: 'center',
      cursor: 'pointer',
      pointerEvents: 'auto',
     };
    let transformPropValue_datetime = (input) => {
      var d = new Date();
      var n = d.toLocaleString();
      //this.props.appActions.updateDataSlot('ds_date', n);
      n = n.replace( /,/g, "" );
      input = n;
      return input;
    }
    
    const value_datetime = transformPropValue_datetime((this.props.appActions.dataSlots ? this.props.appActions.dataSlots['ds_date'] : ''));
    
    const style_elDatetime = {
      color: 'rgba(0, 0, 0, 0.8500)',
      textAlign: 'left',
     };
    const style_elDatetime_outer = {
      display: 'none',
     };
    
    return (
      <div className="Guest appBg">
        <div className="layoutFlow">
          <div className="elTxtFirstName">
            <input className="baseFont" style={style_elTxtFirstName} type="text" placeholder={this.props.locStrings.guest_field_451323} onChange={this.textInputChanged_txtFirstName} value={this.state.txtFirstName}  />
          </div>
          
          <div className="elTxtLastName">
            <input className="baseFont" style={style_elTxtLastName} type="text" placeholder={this.props.locStrings.guest_field2_327248} onChange={this.textInputChanged_txtLastName} value={this.state.txtLastName}  />
          </div>
          
          <div className="elButton">
            <Button className="systemFontRegular" style={style_elButton}  color="accent" onClick={this.onClick_elButton} >
              {this.props.locStrings.user2_button_236176}
            </Button>
          </div>
          
          <div className="flowRow flowRow_Guest_elDatetime_526810">
          <div className="elDatetime" style={style_elDatetime_outer}>
            <div className="baseFont" style={style_elDatetime}>
              <div>{value_datetime !== undefined ? value_datetime : (<span className="propValueMissing">{this.props.locStrings.user2_datetime_526810}</span>)}</div>
            </div>
          </div>
          
          </div>
        </div>
        
      </div>
    )
  }
  
}
