import React, { Component } from 'react';
import './App.css';

// UI framework component imports
import Button from 'muicss/lib/react/button';

export default class Component1 extends Component {

  // This component doesn't use any properties

  constructor(props) {
    super(props);
    
    this.state = {
      field: (this.props.appActions.dataSlots ? this.props.appActions.dataSlots['ds_filter'] : '') || '',
    };
  }

  componentDidMount() {
  }

  componentWillUnmount() {
  }

  componentDidUpdate() {
  }

  componentWillReceiveProps(nextProps) {
  }

  textInputChanged_field = (event) => {
    this.setState({field: event.target.value});
    this.props.appActions.updateDataSlot("ds_filter", event.target.value);
  }
  
  onClick_elButton = (ev) => {
    this.props.appActions.updateDataSlot('ds_filter', "");
  
    // Clear internal state for data-providing elements in this component
    this.setState({
      field: "",
    });
  
  }
  
  
  render() {
    const value_field = this.state.field;
    
    const style_elField = {
      display: 'block',
      backgroundColor: 'white',
      paddingLeft: '1rem',
      boxSizing: 'border-box', // ensures padding won't expand element's outer size
      pointerEvents: 'auto',
     };
    const style_elButton = {
      display: 'block',
      color: 'white',
      textAlign: 'center',
      cursor: 'pointer',
      pointerEvents: 'auto',
     };
    
    return (
      <div className="Component1 appBg">
        <div className="layoutFlow">
          <div className="elField">
            <input className="baseFont" style={style_elField} type="text" placeholder={this.props.locStrings.component1_field_1037213} onChange={this.textInputChanged_field} value={value_field !== undefined ? value_field : ''}  />
          </div>
          
          <div className="elButton">
            <Button className="actionFont" style={style_elButton}  color="accent" onClick={this.onClick_elButton} >
              {this.props.locStrings.component1_button_981651}
            </Button>
          </div>
        </div>
        
      </div>
    )
  }
  
}
